<template>
  <!--  支付二维码  -->
  <el-dialog
    class="custom-dialog payment-dialog"
    width="460px"
    lock-scroll
    append-to-body
    :close-on-click-modal="false"
    :title="`${paymentTypeName}付款`"
    :model-value="visible"
    @open="handleOpen"
    @close="handleClose"
  >
    <div class="body-container">
      <div class="QR-code" id="qrcode"></div>
      <p class="word">付款金额</p>
      <p class="amount">¥ {{ paymentMoney }}</p>
      <p class="prompt">
        请使用
        {{ paymentTypeName }} 扫一扫二维码完成支付
      </p>
      <el-button type="primary" @click="confirmPayment">已完成支付</el-button>
    </div>
  </el-dialog>
</template>

<script lang="ts" setup>
import { computed, shallowReactive } from "vue";

import { useQRCode, useVueRouter } from "@/assets/useHook";
import { saasPaymentTypeMap } from "@assets/typeStatus";

const props = defineProps<{
  visible: boolean;
  paymentType: string;
  paymentLink: string;
  paymentMoney: string;
}>();
const emits = defineEmits<{
  (e: "update:visible", value: boolean);
}>();

const { goPage } = useVueRouter();
const paramsData = shallowReactive({
  paymentVisible: false,
  qrcode: "",
});
const paymentTypeName = computed(() => {
  return saasPaymentTypeMap.get(props.paymentType);
});

const handleOpen = () => {
  if (props.paymentLink) {
    getQRCode(props.paymentLink);
  }
};
const handleClose = () => {
  emits("update:visible", false);
  (document as any).getElementById("qrcode").innerHTML = "";
};
const confirmPayment = async () => {
  await emits("update:visible", false);
  await goPage("/orderManagement/customerOrder/list");
};
const getQRCode = (paymentUrl) => {
  paramsData.qrcode = useQRCode({
    dom: "qrcode",
    text: paymentUrl,
  });
};
</script>

<style lang="scss" scoped>
.body-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  .QR-code {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 168px;
    height: 168px;
    margin-bottom: 40px;
    border: 1px solid #e1e4e8;
  }

  .word {
    font-size: 14px;
    color: #242934;
    line-height: 1;
  }

  .amount {
    font-size: 26px;
    font-weight: 600;
    color: #f12525;
    line-height: 1;
    margin: 12px 0 28px;
  }

  .prompt {
    font-size: 14px;
    color: #9ca6b9;
    line-height: 1;
    margin-bottom: 40px;
  }
}
</style>
<style lang="scss">
.payment-dialog.custom-dialog.el-dialog {
  .el-dialog__body {
    padding: 40px 0 50px;
  }
}
</style>
